













.home {
  margin: 0;
  padding: 0;
}
.home .homain {
  width: 100%;
  height: 5rem;
  border: 0.02rem solid #f00;
  box-sizing: border-box;
}
.home .homain .maincon {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}
.home .homain .maincon .cil {
  border-radius: 50%;
  border: 0.02rem solid #f00;
  text-align: center;
  padding-top: 0.3rem;
  box-sizing: border-box;
}
.home .homain .maincon .smcil {
  width: 20%;
  height: 1.7rem;
}
.home .homain .maincon .bgcil {
  width: 30%;
  height: 2rem;
}
.logincon {
  border: 1px solid #f00;
}
.logincon .formcon {
  margin: auto;
  display: flex;
  flex-flow: column;
  padding: 1rem;
}
.logincon .formcon>div {
  flex: 1;
  width: 100%;
  margin: 0.2rem auto;
}
.logincon .formcon .formgroup>input {
  height: 0.8rem;
  width: 80%;
  margin: auto;
  border: 0.01rem solid #999;
  font-size: 0.4rem;
  padding-left: 0.2rem;
}
.logincon .formcon .formbtn {
  display: flex;
  justify-content: space-around;
}
.logincon .formcon .formbtn>button {
  border: 0.01rem solid #999;
  width: 2rem;
  height: 1rem;
  border-radius: 0.2rem;
  font-size: 0.4rem;
}
